.auth__loading-title{
    color: $dark-grey;
    text-align: center;
    
}

.spinner{   


margin-left: .4rem;

animation: loading-spinner 2s linear infinite;  

}
@keyframes loading-spinner
 {
    0%{
    transform: rotate(0deg);
    }
    100%{
    transform: rotate(360deg);
    }
 }
